import React from 'react';
import StripeCheckout from 'react-stripe-checkout';
import axios from 'axios';

const StripeCheckoutButton = ({ price }) => {
  const priceForStripe = price * 100;
  const publishableKey = 'pk_test_51H0ir0AyvULANtQRbAun6nutc7nOKY58fw2399IfpdyL4nmZCHWUag7dE68YQUf6Udc51zETQEYloBMSZm10y6TE00mrIvqRV6';

  const onToken = token => {
    axios({
      url: 'payment',
      method: 'post',
      data: {
        amount: priceForStripe,
        token: token
      }
    })
      .then(response => {
        alert('succesful payment');
      })
      .catch(error => {
        console.log('Payment Error: ', error);
        alert(
          'There is an issue with your payment!, Please contact us'
        );
      });
  };

  return (
    <StripeCheckout
      label='Pay Now'
      name='Jerry Clothing Store.'
      billingAddress
      shippingAddress
      image='https://svgshare.com/i/CUz.svg'
      description={`Your total is $${price}`}
      amount={priceForStripe}
      panelLabel='Pay Now'
      token={onToken}
      stripeKey={publishableKey}
    />
  );
};

export default StripeCheckoutButton;
